import React, { useState, useEffect, } from 'react';
import RcIf from 'rc-if';
import SweetAlert from 'sweetalert2-react';
import api from '../../service/api'
import Modal from 'react-bootstrap/Modal'
import Button from 'react-bootstrap/Button'

import mainImage from '../../assets/user2.png'
import userIcon from '../../assets/user2.png'
import padlockIcon from '../../assets/padlock.png'
 
import './Register.css';

/*
const header = {
    "Access-Control-Allow-Origin": "*",
    "Access-Control-Allow-Headers": "Content-Type,Content-Length, Authorization, Accept,X-Requested-With",
    "Access-Control-Allow-Methods": "PUT,POST,GET,DELETE,OPTIONS"
}
*/

export default function Register({ history }) {
    const [ email, setEmail ] = useState('');
    const [ senha, setSenha ] = useState('');
    const [ nome, setNome ] = useState('');
    const [ professor, setProfessor ] = useState();
    const [ cadastro, setCadastro ] = useState('');

    const [ senha1, setSenha1 ] = useState('');
    const [ senha2, setSenha2 ] = useState('');


    async function efetuarCadastro(e){
        e.preventDefault();
        
        const response = await api.post('/cadastro/usuario',{
            nome: nome,
            email: email,
            senha: senha,
            professor: professor
        });

        if(response.data.user){
            setCadastro('true')
            
        } 
        else {
            history.push(`/${response.data._id}/icon`)
            
        }
        
    }

    useEffect(() => {
        if (senha1 !== senha2){
            console.log("Senha no coincidem")
        } 
        else {
            setSenha(senha2);
            console.log(senha)
        }
        
        if (email.includes('rede.ulbra.br')){
            setProfessor(false);
        } 
        else if(email.includes('ulbra.br')){
            setProfessor(true);
        }
        
        console.log(professor);
        
    })

    const [show, setShow] = useState(false);

    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);

    return (
        
    <div className='main-register'>

        <RcIf if={cadastro === "true"}>
            <SweetAlert
                show={cadastro}
                title="Falha no Cadastro"
                text="E-mail j cadastrado!"
                type="error"
                onConfirm={() => setCadastro(null)}
            />
        </RcIf>
        <div className='form-register'>
            
            <img src={ mainImage } alt='conce de usurio' id='main-image' />

            <hr id='hr-top-left' />
            <hr id='hr-top-right' />

            <h2 id='h2-cadastro'>CADASTRO</h2>

            <form onSubmit={efetuarCadastro}> 
                <div className='inputs'>
                    {/*PASSWORD INPUT*/}
                    
                    <label className='label-name-input' for='name-input'> Digite seu nome: </label>

                    <div className='name'>
                        <div className='div-user-icon'>
                            <img id='user-icon' src={ userIcon } alt='imagem usurio' />
                        </div>
                        <div>
                            <input  className="form-control" id="name-input" aria-describedby="nameHelp"
                                placeholder="Digite seu nome" name='name' type="text" value={nome} onChange={e => setNome(e.target.value)} required />
                        </div>
                    </div>

                    <label className='label-email-input' for='email-input'> Digite seu e-mail: </label>
                    
                    <div className='email'>
                        <div className='div-user-icon'>
                            <img id='user-icon' src={ userIcon } alt='imagem usurio' />
                        </div>
                        <div>
                            <input  className="form-control" id="email-input" aria-describedby="emailHelp"
                                placeholder="usuario@rede.ulbra.br" name='email' type="text" value={email} onChange={e => setEmail(e.target.value)} required />
                        </div>
                    </div>

                    {/*PASSWORD INPUT*/}
                    <label className='label-password-input' for='password-input'> Digite sua senha: </label>
                    <div className='password'>
                        <div className='div-padlock-icon'>
                            <img id='padlock-icon' src={ padlockIcon } alt='padlock' />
                        </div>
                        <input type="password" className="form-control" name="senha" id="password-input" 
                            placeholder="Senha" value={senha1} onChange={e => setSenha1(e.target.value)} required/>
                    </div>
                

                    {/*PASSWORD INPUT CONFIRMATION*/}
                    <label className='label-password-input-confirmation' for='password-input-confirmation'> Confirme sua senha: </label>
                    <div className='password-confirmation'>
                        <div className='div-padlock-icon-confirmation'>
                            <img id='padlock-icon-confirmation' src={ padlockIcon } alt='padlock' />
                        </div>
                        <input type="password" className="form-control" name="password-confirmation" id="password-input-confirmation" 
                            placeholder="Confirme sua senha" value={senha2} onChange={e => setSenha2(e.target.value)} required/>
                    </div>
                </div>
                    
                <div className="div-accept-terms">

                    <Button className='btn btn-outline-info terms-use' onClick={handleShow} required>
                        Termos de uso
                    </Button>
                    
                    <Modal show={show} onHide={handleClose}>
                    <Modal.Header closeButton>
                        <Modal.Title>Poltica de Privacidade</Modal.Title>
                    </Modal.Header>
                    <Modal.Body className='terms'>
                        
                        <p align="justify">
                            A BattleClass Inc. ("BattleClass") leva a privacidade de seus alunos, professores e pais a srio. 
                            A BattleClass  est comprometida em proteger a privacidade dos usurios, proporcionando uma experincia 
                            de aprendizado personalizada e valiosa.
                        </p>
                        <p align="justify">
                            A BattleClass aceitou o Compromisso de Privacidade do Estudante como um compromisso pblico pela 
                            coleta e uso responsveis dos dados do aluno. Entre outras coisas, comprometemo-nos a coletar apenas 
                            as informaes dos alunos necessrias para fins educacionais/escolares autorizados, ou conforme 
                            autorizado pelos pais/aluno, para no vender informaes pessoais dos alunos, no usar as informaes 
                            dos alunos para direcionamento comportamental de anncios aos alunos. e no reter informaes pessoais 
                            do aluno alm do perodo necessrio para apoiar os propsitos educacionais/escolares autorizados. 
                            Todas as informaes pessoais relativas a membros, assinantes, clientes ou visitantes que usem o 
                            BattleClass  sero tratadas em concordncia com a Lei da Proteo de Dados Pessoais de 26 de outubro 
                            de 1998 (Lei n. 67/98). A BattleClass  desenvolveu esta Poltica de Privacidade para informar aos 
                            usurios em termos gerais sobre as prticas de gerenciamento de informaes pessoais da Classcraft, 
                            incluindo quais informaes pessoais so coletadas e como so processadas e usadas. Se os usurios 
                            tiverem alguma dvida sobre nossa Poltica de Privacidade, podero entrar em contato com a BattleClass  
                            em contato.devs@gmail.com.
                        </p>

                        <strong>O ambiente escolar</strong>

                        <p  align="justify">Como o BattleClass  foi desenvolvido para ser jogado em grupo, muitas das informaes coletadas sobre 
                        professores e alunos devem ser compartilhadas. O BattleClass  armazena as seguintes informaes que o 
                        professor e os alunos:</p>

                        <ul>
                            <li>Nome do aluno ou professor;</li>
                            <li>Endereo de e-mail do aluno ou professor;</li>
                            <li>Atividades de jogo para estudantes ou professores;</li>
                            <li>Contedo gerado pelo professor (por exemplo: postagens, tarefas, questionrios etc.);</li>
                        </ul>

                        <p  align="justify">
                            Entende-se que o professor publica as  atividades, informaes ou comunicaes relacionadas ao jogo ou 
                             classe podem ser visveis para professores.  O compartilhamento das informaes acima tambm est 
                            sujeito s polticas de privacidade das instituies educacionais relevantes. Se os usurios tiverem 
                            alguma dvida sobre o compartilhamento dessas informaes, a BattleClass  recomenda que os usurios 
                            entrem em contato com as instituies educacionais relevantes.
                        </p>
                        <p  align="justify">
                            A BattleClass  implementou medidas de segurana para garantir que esses dados no sejam compartilhados 
                            fora do ambiente escolar, exceto conforme descrito nesta Poltica de Privacidade.
                        </p>

                        <strong>Informaes coletadas</strong>
                        <p  align="justify">1. Informaes Pessoais</p>

                        <p  align="justify">A BattleClass  coleta informaes pessoais para os seguintes propsitos:</p>
                        <ul>
                            <li>Efetuando login no aplicativo;</li>
                            <li>Operando o jogo;</li>
                            <li>Entrega de tarefas e concluso de tarefas no aplicativo;</li>
                        </ul>

                        <p  align="justify">As informaes coletadas para as contas do PROFESSOR podem incluir:</p>
                        <ul>
                            <li>Endereo de e-mail;</li>
                            <li>Dados de jogo;</li>
                            <li>Contedo gerado pelo professor (por exemplo: postagens, tarefas, questionrios etc.);</li>
                            <li>Notas de atribuio;</li>
                            <li>Atividade do usurio (por exemplo: logins, pginas visualizadas, etapas de configurao da 
                            conta concludas etc.).</li>
                        </ul>

                        <p  align="justify">As informaes coletadas para as contas dos ALUNOS esto limitadas a:</p>
                        <ul>
                            <li>Nome de usurio;</li>
                            <li>Endereos de e-mail institucional;</li>
                            <li>Nome e sobrenome;</li>
                            <li>Dados de jogo (por exemplo: concesso de XP, HP, escolha de seus avatares, etc.);</li>
                            <li>Notas da tarefa </li>
                            <li>Contedo gerado pelo aluno (por exemplo: respostas a questionrios etc.);</li>
                        </ul>

                        <p  align="justify">BattleClass no coleta os seguintes dados:</p>
                        <ul>
                            <li>Dados biomtricos fisiolgicos;</li>
                            <li>Informaes sobre o status de almoo grtis ou reduzido;</li>
                            <li>Listas de contatos ou listas de amigos do usurio;</li>
                            <li>Notas de avaliao/ relacionadas diretamente a instituio de ensino;</li>
                        </ul>

                        <strong>O que a BlattleClass faz com as informaes coletadas?</strong>
                        <p  align="justify">As informaes coletadas so usadas para os seguintes propsitos:</p>
                        <ul>
                            <li>Permitir que o professor gerencie o jogo;</li>
                            <li>Permitir que colegas joguem o jogo;</li>
                            <li>Habilitar atividades relacionadas  classe, como contedo da sala de aula, tarefas e comunicaes;</li>
                            <li>Para fins de suporte de software;</li>
                        </ul>
                        
                        <p  align="justify">
                            A BattleClass  no compartilha informaes de identificao pessoal fora do ambiente escolar, exceto 
                            para os fins estabelecidos neste documento, sem primeiro obter o consentimento expresso por escrito 
                            da pessoa.
                        </p>

                        <strong>Privilgios de acesso de usurio da BattleClass</strong>
                        <p  align="justify">
                             concedido aos alunos acesso a seus dados pessoais, dados de jogo em equipe, todo o contedo 
                            postado pelo professor no sistema de contedo da sala de aula.
                        </p>

                        <strong>Registros de Alunos</strong>
                        <p  align="justify">
                            Os registros dos alunos so de propriedade e esto sob o controle da escola e/ou distrito escolar. 
                            Os alunos podem acessar seus registros de estudante fazendo login na conta de aluno.
                        </p>
                        <p  align="justify">
                            Terceiros no tm acesso a informaes de identificao pessoal, registros educacionais ou outros 
                            registros de alunos, exceto conforme estabelecido nesta Poltica de Privacidade, ou aps obter primeiro 
                            o consentimento expresso por escrito dos alunos.
                        </p>
                        <p  align="justify">
                            Se houver divulgao no autorizada dos registros de um aluno, a BattleClass  notificar a instituio, 
                            o professor, e o aluno por e-mail dentro de at 3 dias aps a descoberta da divulgao no autorizada.
                        </p>

                        <strong>Sem marketing para alunos.</strong>
                        <p  align="justify">
                            A BattleClass  no compartilha dados do usurio com terceiros para fins de marketing. Alm disso, 
                            a BattleClass  no usa dados do aluno para comercializao de qualquer tipo, incluindo marketing para 
                            o aluno e para os pais.
                        </p>

                        <strong>Excluso de registros de alunos</strong>
                        <p  align="justify">
                            Os seguintes registros do aluno so retidos por um perodo no superior a 12 meses aps a ltima entrada 
                            na conta do aluno no jogo:
                        </p>

                        <ul>
                            <li>Informaes do jogo do aluno, e todos os dados relacionados, incluindo datas, descries e valores de pontos;</li>
                            <li>Qualquer outra informao do aluno que no possa ser usada pelo aluno nos prximos anos.</li>
                        </ul>

                        <p  align="justify">Alunos e professores podem solicitar a excluso de sua conta e as informaes relacionadas do banco 
                        de dados da BattleClass, entrando em contato em contato.devs@gmail.com.
                        </p>

                        <strong>Excluso de registros de professores</strong>
                        <p  align="justify">
                            Os registros dos pais sero retidos por um perodo no superior a 36 meses aps a ltima entrada feita 
                            em uma das contas dos alunos de seus filhos. Esse perodo foi selecionado para facilitar o registro 
                            simplificado do BattleClass durante toda a educao de seus filhos.
                        </p>

                        <p  align="justify">
                            Os dados pessoais dos professores e todos os dados ainda no excludos relacionados s aulas de um 
                            professor sero excludos 5 anos aps a inatividade da conta.
                        </p>

                        <strong>Conformidade regional</strong>
                        <p  align="justify">1. Brasil</p>

                        <strong>Marco Civil da Internet</strong>
                        <p  align="justify">O marco civil da internet estabelece princpios, garantias, direitos e deveres para o uso da 
                            internet no Brasil e determina as diretrizes para atuao da Unio, dos Estados, do Distrito Federal 
                            e dos Municpios em relao  matria. A poltica de privacidade da BattleClass  projetada para 
                            proteger os dados dos alunos em conformidade com o Marco Civil da Internet.
                        </p>

                        <p  align="justify">
                            A disciplina do uso da internet no Brasil tem como fundamento o respeito  liberdade de expresso, bem como:
                        </p>

                        <p  align="justify">I - o reconhecimento da escala mundial da rede;</p>
                        <p  align="justify">II - os direitos humanos, o desenvolvimento da personalidade e o exerccio da cidadania em meios digitais;</p>
                        <p  align="justify">III - a pluralidade e a diversidade;</p>
                        <p  align="justify">IV - a abertura e a colaborao;</p>
                        <p  align="justify">V - a livre iniciativa, a livre concorrncia e a defesa do consumidor; e</p>
                        <p  align="justify">VI - a finalidade social da rede.</p>
                        <p  align="justify">Art. 3 A disciplina do uso da internet no Brasil tem os seguintes princpios:</p>
                        <p  align="justify">I - garantia da liberdade de expresso, comunicao e manifestao de pensamento, nos termos da 
                            Constituio Federal;</p>
                        <p  align="justify">II - proteo da privacidade;</p>
                        <p  align="justify">III - proteo dos dados pessoais, na forma da lei;</p>
                        <p  align="justify">IV - preservao e garantia da neutralidade de rede;</p>
                        <p  align="justify">V - preservao da estabilidade, segurana e funcionalidade da rede, por meio de medidas tcnicas 
                            compatveis com os padres internacionais e pelo estmulo ao uso de boas prticas;</p>
                        <p  align="justify">VI - responsabilizao dos agentes de acordo com suas atividades, nos termos da lei;</p>
                        <p  align="justify">VII - preservao da natureza participativa da rede;</p>
                        <p  align="justify">VIII - liberdade dos modelos de negcios promovidos na internet, desde que no conflitem com os 
                            demais princpios estabelecidos nesta Lei.</p>

                        <strong>Poltica de segurana</strong>
                        <p  align="justify">
                            A BattleClass  usa mtodos padro do setor para proteger os dados de seus clientes contra uso ou acesso 
                            no autorizado, alterao, destruio ou perda ilegal ou acidental. No entanto, deve-se notar que no  
                            possvel garantir 100% devido a constantes avanos na tecnologia de vrus e hackers. A BattleClass no 
                            pode, portanto, ser responsabilizada pela perda ou alterao de dados. Uma cpia completa da Poltica 
                            de Segurana da BattleClass  pode ser obtida mediante solicitao para contato.dev@gmail.com.
                        </p>

                        <strong>Alteraes e acesso a informaes pessoais</strong>
                        <p  align="justify">
                            Os usurios tm acesso s suas informaes pessoais por meio da conta de usurio do BattleClass ou 
                            entrando em contato com contato.dev@gmail.com.
                        </p>

                        <p  align="justify">
                            Os usurios tambm podem solicitar uma cpia de suas informaes pessoais ou fazer modificaes em 
                            qualquer informao incorreta enviando uma solicitao por escrito para contato.dev@gmail.com. 
                            As seguintes informaes devem ser fornecidas:
                        </p>
                        
                        <ul>
                            <li>Primeiro e ltimo nome;</li>
                            <li>O email;</li>
                            <li>Nome da turma e escola;</li>
                            <li>Nome do aluno e/ou ID do usurio e/ou nome de usurio;</li>
                        </ul>

                        <p  align="justify">O usurio ser ento contatado por um membro da equipe BattleClass  para fins de verificao.</p>

                        <p  align="justify">
                            Os usurios tambm podem corrigir e atualizar suas informaes pessoais usando os mesmos procedimentos 
                            descritos acima.
                        </p>

                        <strong>Mudana de Controle</strong>
                        <p  align="justify">
                            Caso toda ou parte da BattleClass ou seus ativos sejam adquiridos ou mesclados com terceiros, as 
                            informaes pessoais que coletamos dos usurios seriam um dos ativos transferidos ou adquiridos por 
                            esse terceiro. Esta Poltica de Privacidade continuar sendo aplicada s suas informaes, e qualquer 
                            adquirente somente poder lidar com suas informaes pessoais de acordo com esta poltica (a menos que 
                            voc d consentimento a uma nova poltica). Ns lhe enviaremos um aviso de aquisio dentro de trinta 
                            (30) dias aps a concluso de tal transao, publicando em nossa pgina inicial ou por e-mail para o 
                            seu endereo de e-mail que voc nos forneceu. Se voc no consentir o uso de suas informaes pessoais 
                            por uma empresa sucessora, poder solicitar sua excluso da empresa.
                        </p>
                        
                        <p  align="justify">
                            No caso improvvel de a BattleClass encerrar as atividades, todas as informaes do usurio sero 
                            excludas com segurana dentro de 12 meses a partir da data de encerramento das atividades.
                        </p>

                        <strong>Divulgao de informaes para cumprir as obrigaes legais</strong>
                        <p  align="justify">
                            A BattleClass  pode divulgar informaes pessoais se acreditarmos de boa f que isso  necessrio 
                            para cumprir a lei, como cumprir uma intimao ou outro processo legal. Podemos precisar divulgar 
                            informaes pessoais onde, de boa f, acharmos necessrio proteger os direitos, propriedade ou 
                            segurana da BattleClass  , nossos funcionrios, nossa comunidade ou outros, ou impedir violaes de 
                            nossos Termos de Servio ou outros acordos. Isso inclui, sem limitao, a troca de informaes com 
                            outras empresas e organizaes para proteo contra fraudes ou resposta a solicitaes governamentais.
                        </p>

                        <strong>Alteraes e atualizaes da Poltica de Privacidade</strong>
                        <p  align="justify">
                            A BattleClass  pode, a seu exclusivo critrio, atualizar, revisar, modificar e suplementar periodicamente 
                            esta Poltica de Privacidade. Se fizermos modificaes nesta poltica ou nos Contratos de Usurio 
                            relacionados, publicaremos um aviso que voc receber quando fizer login no jogo e/ou enviaremos 
                            um email diretamente para voc com uma cpia dos novos documentos. A BattleClass  pede aos usurios 
                            que revejam a Poltica de Privacidade atualizada e/ou os Contratos de usurio antes de continuarem 
                            a usar nossos servios. Se os usurios no aceitarem a Poltica de Privacidade atualizada, os usurios 
                            podero cancelar suas assinaturas de acordo com o Contrato de Licena do Professor, os Termos de Uso do 
                            Estudante e/ou os Termos de Uso dos Pais.
                        </p>
                        
                        <strong>Consentimento para a coleta e processamento de informaes</strong>
                        <p  align="justify">
                            Ao aceitar o Contrato de Licena do Professor, os Termos de Uso dos Pais e/ou os Termos de Uso dos 
                            Estudantes esto expressamente concedendo  BattleClass  uma declarao especial de que os usurios 
                            decidiram, por livre e espontnea vontade, conceder autorizao especfica  BattleClass  para coletar, 
                            processar e usar as informaes pessoais estipuladas nessa Poltica de Privacidade e para usar essas 
                            informaes de acordo com o que  estipulado nesta Poltica de Privacidade. Os usurios esto declarando 
                            ainda que esto cientes do objetivo da BattleClass  de coletar, processar e usar essas informaes, 
                            como o processamento ser conduzido, como a privacidade dos usurios ser protegida e que os usurios 
                            esto autorizados a retirar seu consentimento.
                        </p>
                        
                        <strong>Contato</strong>
                        <p  align="justify">
                            O servio da BattleClass  aos seus usurios e a confiana dos usurios so de extrema importncia. 
                            Se os usurios tiverem alguma dvida sobre esta Poltica de Privacidade, os usurios devem entrar 
                            em contato com a BattleClass em:
                        </p>
                        <strong>E-mail:</strong> contato.devs@gmail.com

                    </Modal.Body>
                    <Modal.Footer>
                        <Button variant="secondary" onClick={handleClose}>
                            Fechar
                        </Button>
                        <Button variant="primary" onClick={handleClose} required>
                            Aceitar
                        </Button>
                    </Modal.Footer>
                    </Modal>

                </div>
                
                <button className="btn btn-primary enter-button"> Criar </button> 

                <div className='link'>
                    <a href='/frontend-battleclass/' id='link-login'> J possui cadastro? </a>
                </div>
                
            </form>
        </div>

    </div>

    
        
   

    );
}

